            <yd-navbar-back-icon>Back</yd-navbar-back-icon>
        </router-link>
    </yd-navbar>

    <div class="tags"> 
        
<video-player  class="video-player-box"
                 ref="videoPlayer"
                 :options="playerOptions"
                 :playsinline="true"
                 customEventName="customstatechangedeventname"

                 @play="onPlayerPlay($event)"
                 @pause="onPlayerPause($event)"
                 @ended="onPlayerEnded($event)"
                 @waiting="onPlayerWaiting($event)"
                 @playing="onPlayerPlaying($event)"
                 @loadeddata="onPlayerLoadeddata($event)"
                 @timeupdate="onPlayerTimeupdate($event)"
                 @canplay="onPlayerCanplay($event)"
                 @canplaythrough="onPlayerCanplaythrough($event)"

                 @statechanged="playerStateChanged($event)"
                 @ready="playerReadied">
  </video-player>


        <yd-flexbox v-for="item, key in list" :key="key" class="item">
            <yd-flexbox-item>
                <div class="desc-wrapper">
                <div class="title"> 
                    
                    <h1 style="display:inline-block; margin-right:15px; font-size:26px; margin-bottom:15px" class="tagname bigtitle">{{item.tagname}}</h1>
                    
                    <iframe src="https://ghbtns.com/github-btn.html?user=twbs&repo=bootstrap&type=star&count=true&size=large" frameborder="0" scrolling="0" width="160px" height="30px"></iframe>
                    <p> From: {{item.from}}</p>
                    <span>Score:</span> 
                <el-rate
                v-model="4.6"
                disabled
                show-score
                text-color="#ff9900"
                score-template="{value}">
                </el-rate>

                    <p>Description: {{item.desc}}</p>
                
                    <span class="price"> Duration: {{item.duration}} months</span>
                </div>

