                    <img  src="https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg">
                    <a style="display:block;">Cool mentor</a> 
            </div>
            <yd-flexbox-item class="review-text">oh,I like this intern, the best I had before...stronglly recommended..</yd-flexbox-item>
        </yd-flexbox>

