                    <img  src="https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg">
                     <a style="display:block;">Mad mentor</a>
            </div>
            
            <yd-flexbox-item class="review-text">This student is so lazy..I wouldn't recommend him again...</yd-flexbox-item>
           
        </yd-flexbox>
         <yd-flexbox class="review chela-master">
             <div class="profile-wrapper">
                    <img  src="https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg">
                     <a style="display:block;">Mad mentor</a>
            </div>
            
            <yd-flexbox-item class="review-text">This student is so lazy..I wouldn't recommend him again...</yd-flexbox-item>
           
        </yd-flexbox>
         <yd-flexbox class="review chela-master">
             <div class="profile-wrapper">
                    <img  src="https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg">
                     <a style="display:block;">Mad mentor</a>
            </div>
            
            <yd-flexbox-item class="review-text">This student is so lazy..I wouldn't recommend him again...</yd-flexbox-item>
           
        </yd-flexbox>
         <yd-flexbox class="review chela-master">
             <div class="profile-wrapper">
                    <img  src="https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg">
