            border-bottom 1px solid #efefef
            padding 10px
        width 100%
        display inline-block
        .profile-wrapper

            display inline-block
            position flex
            max-width 100px
            img
                width 50%
                border-radius 50%

        .desc-wrapper
            .yd-badge-hollow
                margin-right 10px
            display inline-block
            position flex
            width 100%
            padding 10px
            .title
                text-align left 
            .desc
                text-align left
                padding 10px 0
                .action
                    margin-top 10px
</style>



